* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App .logo{
  width: 80%;
  max-width: 320px;
  margin: 20px auto;
  display: block;
}

.App .search-block {
  width: 95%;
  height: 30px;
  max-width: 400px;
  margin: 10px auto;
  border-radius: 5px;
  border: 1px solid #219ea9;
  overflow: hidden;
}

.App .search-block input {
  width: calc(100% - 140px);
  height: 100%;
  border: 0;
  padding-left: 10px;
}

.App .search-block .search-button {
  width: 50px;
  height: 100%;
  background-color: #00E6E6;
  border: 0;
  color: white;
  cursor: pointer;
}

.App .search-block .remove-button {
  width: 90px;
  height: 100%;
  background-color: #219ea9;
  border: 0;
  color: white;
  cursor: pointer;
}

.App .joke-list {
  width: 95%;
  max-width: 400px;
  min-height: 300px;
  margin: 0 auto;
  border: 1px solid #219ea9;
  border-radius: 5px;
}

.App .joke-list ul li {
  padding: 10px;
  border-bottom: 1px solid #219ea9;
  list-style: none;
  text-align: center;
}

.App .joke-list ul li:nth-last-child(1) {
  border: 0;
}

.App .joke-list ul li .line {
  width: 100%;
  height: 2px;
  background: #ccc;
  display: block;
  margin: 5px 0;
}

.App .joke-list ul li p {
  text-align: justify;
  word-break: break-all;
}